$yellow: #ffe034;

@import 'normalize';
@import 'typography';
// @import 'navigation';
@import 'sidebar';
@import 'comments';
@import 'footer';
/*
====================================
Default Styling
====================================
*/

html {
	background: #F5F5F5;
	color: #333;
}

.wrapper {
	width: 95%;
	max-width: 1080px;
	margin: 0 auto;
}

.landing {
	width: 100%;
	height: 100vh;
	background: #333;
	position: fixed;
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 20;

	h1 {
		font-size: 10rem;
		color: #FFF;
		visibility: hidden;
		opacity: 0;
		transition: all 1s;
	}
}

.hero-header {
	width: 100%;
	height: 100vh;
	padding: 25px;
	position: fixed;
	background-size: cover;
	color: #FFF;
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 10;

	.wrapper {
		width: 98%;
		max-width: 1920px;
	}
}

.hero-team-select {
	width: 100%;
	// margin: 50px 0;
	visibility: hidden;
	opacity: 0;
	display: flex;
	flex-flow: row wrap;
	justify-content: center;
	align-items: flex-start;
	transition: all .8s;

	input[type=radio] {
		display: none;
	}
}

.hero-team {
	width: calc(100% / 7);
	margin-bottom: 50px;
	padding: 0 15px;
	display: flex;
	flex-flow: column nowrap;
	text-align: center;

	img {
		height: 95px;
		margin: 0 auto 10px;
		display: block;
	}

	h3 {
		transition: all .25s;
	}

	&:nth-child(n+12) {
		margin-bottom: 0;
	}

	&:hover {
		&.hero-team h3 {
			color: $yellow;
		}
	}
}

fieldset {
	border: 0;
	margin: 0;
	padding: 0;
}

label {
	cursor: pointer;
}

.team-info-screen {
	// height: 0;
}

.teams-nav {
	height: 70px;
	background: #F5F5F5;

	.wrapper {
		height: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	a {
		color: #333;

		&:hover {
			color: $yellow;
		}
	}
}

.teams-nav-right {
	height: 70px;
	display: flex;
	flex-flow: column nowrap;
	justify-content: space-between;
	align-items: flex-end;
}

.nav-team-select, .nav-main-nav {
	height: 35px;
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
}

.nav-team-select {
	input[type=radio] {
		display: none;
	}
}

.nav-team {
	height: 20px;
	margin: 0 6px;
	transition: all .15s;

	img {
		height: 100%;
		display: block;
	}

	&:last-child {
		margin-right: 0;
	}

	&:hover {
		height: 25px;
	}
}

.nav-main-nav {
	ul {
		height: 35px;
		margin: 0;
		padding: 0;
		display: flex;
		flex-flow: row nowrap;


		li {
			padding: 5px 10px;
			display: inline-block;
			transition: all .25s;

			&:hover {
				background: $yellow;
			}
		}

		a {
			font-family: 'Titillium Web', sans-serif;
			font-size: 1.8rem;
			text-transform: uppercase;

			&:hover {
				color: #333;
			}
		}
	}
}

.team-info-hero {
	width: 100%;
	height: 50vh;
	padding: 50px 25px;
	margin-bottom: 50px;
	color: #FFF;

	.wrapper {
		height: 100%;
		display: flex;
		flex-flow: column nowrap;
		justify-content: space-between;
		align-items: center;
	}

	h2 {
		color: $yellow;
	}
}

.team-info {
	width: 100%;
	height: 100px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.team-name {
	height: 100%;
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
}

.team-emblem {
	height: 100px;
	margin-right: 20px;

	img {
		height: 100%;
		display: block;
	}
}

.team-total-salary {
	text-align: right;
}

.main-team-stats {
	width: 100%;
	display: flex;
	flex-flow: row wrap;
	justify-content: space-between;
	align-items: center;
}

.main{
	display:flex;
	justify-content: space-between;
}

.left{
	width:66%;
}

.right{
	width:30%;

	.latestPosts,
	.tagCloud{
		margin-bottom:25px;
		padding:15px;
	}

	.latestPosts {
		background:#333;
		color:white;

		a {
			color: $yellow;

			&:hover {
				color: #333;
			}
		}
	}

	.content-featured-post{
		display:flex;
		margin:10px 0;
	}

	.featured-blog-post img{
		width:75px;
		height:75px;
	}

	.featured-blog-post p:nth-of-type(1){
		margin:0;
		font-size:12px;
	}

	.blog-post-info{
		margin:10px;
		margin-top:0;
	}

	.tagCloud{
		background:#d9d9d9;
	}

	.tags{
		margin:10px 0;

		a{
			color:#666;
			border:1px solid #666;
			padding:2px 5px;
		}
	}

	.teamStandingSidebar{
		// padding:15px;
		color: #333;
		border: 1px solid #DDD;
		transition: all .25s;

		h3 {
			padding: 15px;
		}
	}

	#teamsStandingTable{
		margin-top:0px;
		margin-bottom:0px;
	}

	td {
		font-size: 1.35rem;
		padding: 5px 15px;
	}

	// tr{
	// 	border-bottom: 1px solid #666;
	// 	&:hover{
	// 		background:#4A4947;
	// 	}
	// }

	// td{
	// 	font-size: 1.35rem;
	// 	padding:5px;
	// 	color:white;
	// }
}

.team-cap-hit,
.team-cap-space,
.team-dps{
	text-align: left;

	sup{
		margin-left:5px;
	}
}

.team-cap-hit-info,
.team-cap-space-info,
.team-dps-info{
	position: relative;
	color: $yellow;

	&:hover:after {
		visibility: visible;
		opacity: 1;
	}
}

.team-cap-hit-info:after,
.team-cap-space-info:after,
.team-dps-info:after{
	content:"Heart attack never stopped old big bear. Mr. Zuckerkorn, you've been warned about touching. You said spanking. I am having a love affair with this ice cream sandwich. Hola, is Rosa still alive? No? Well this is not my day.";
	visibility: hidden;
	opacity: 0;
	position:absolute;
	left: -125px;
	top: -160px;
	width:200px;
	background: rgba(50,50,50,.85);
	padding:15px;
	text-align:left;
	color:#fff;
	font-family: 'Open Sans', sans-serif;
	font-size: 1.3rem;
	transition: all .25s;
}

.team-cap-space {
	text-align: center;
}

.team-dps {
	text-align: right;
}

table {
	width: 100%;
	margin: 25px 0;
	border-collapse: collapse;
}

tr {
	border-bottom: 1px solid #EEE;
	color: #666;

	&:hover {
		background: #FFF;
		border-bottom: 1px solid $yellow;
		color: #333;
	}

	&:last-child {
		border-bottom: none;
	}
}

th, td {
	height: 40px;
	padding: 15px;
	font-size: 1.4rem;
	text-align: left;
	vertical-align: center;
}

th {
	background: #FFF;
	border-bottom: 2px solid $yellow;
}

.dp-icon {
	padding: 4px 10px;
	margin-left: 5px;
	display: inline-block;
	background: $yellow;
	font-size: 1rem;
	position: relative;
	top: -2px;
	&:hover:after{
		visibility: visible;
		opacity: 1;
	}
}

.dp-icon:after{
	content:"Heart attack never stopped old big bear. Mr. Zuckerkorn, you've been warned about touching. You said spanking. I am having a love affair with this ice cream sandwich. Hola, is Rosa still alive? No? Well this is not my day.";
	visibility: hidden;
	opacity: 0;
	position:absolute;
	left: -125px;
	top: -140px;
	width:200px;
	background: rgba(50,50,50,.85);
	padding:15px;
	text-align:left;
	color:#fff;
	font-size:12px;
	transition: all .25s;
}

//BLOG PAGE

article.post-loop{
	margin:20px;
}

section.entry-content{
	display:flex;
	background:white;
	border:2px solid $yellow;
	&:hover figcaption{
		background:rgba(255,199,34, 0.5);
	}


	.featured-img{
		width:calc(300px);
		height:300px;
		position:relative;

		.triangle{
			border:20px solid white;
			height:0;
			width:0;
			border-top:20px solid transparent;
			border-left:20px solid transparent;
			border-bottom:20px solid transparent;
			position:absolute;
			top:130px;
			right: 0;
		}

		figcaption{
			position:absolute;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
			transition:all 0.5s ease;
		}
	}

	aside.post-preview{
		display:flex;
		flex-direction:column;
		justify-content:center;
		margin:0 20px;
		width:50%;
		h6, 
		p{
			font-size:10px;
			color:grey;
			margin-bottom:10px;
		}
		
		h2{
			margin-bottom:10px;
		}

		.read-more{
			color:grey;
			background-color:yellow;
			padding:3px 10px;
			border:1px solid grey;
			transition:all 0.5s ease;
			font-size:10px;
			&:hover{
				background:grey;
				color:yellow;
				border:1px solid yellow;
			}
			&:focus{
				background:grey;
				color:yellow;
				border:1px solid yellow;
			}
		}

		.post-foot{
			margin-top:10px;
		}
	}
}

//sidebar

.blog-content{
	display:flex
}

.posts{
	width:66%;
}

.sidebar{
	width:33%;
	padding:20px;

	#recent-comments-2,
	#archives-2,
	#categories-2{
	// padding:15px;
	color: #333;
	border: 1px solid #DDD;
	transition: all .25s;
	margin-bottom:25px;
	padding:15px;

		h3 {
			padding: 15px;
		}

		li {
			font-size: 1.35rem;
			padding: 5px 15px;
		}
	}
}

//SINGLE page